@use '/src/utils/'as *;

* {
  padding: 0;
  margin: 0;
  font-family: $main-font;
}

body {
  $color: get-color(da1);

  background-color: $color;
}

.quote {
  display: flex;
  justify-content: center;
  align-items: center;

  p {
    $color: get-color(li1);

    color: $color;
    font-size: 4rem;
    padding: 5rem;
  }
}
